import { eApiCaller, eDate, eError, eSuccess } from "../models/core/my-js";

export const ebAPIs = new eApiCaller(
    "https://dev.exabytellc.com/_/Exa/BE/ERP/API/", {
    defaultHeaders: {
        "User-Timezone": eDate.now.timezoneOffset,
    },
    defaultBodyData: {
        "SID": "UXZFWHcyM0daVCtRZEE9PTpvMzFzZUJybHZHUjUvNVFhMmdqczNBPT0=", // OTH SID
    },
    bodyDataType: "formdata",
    responseType: "json",
    responseHandler: (request) => {
        // request error
        if (request.httpCode !== 200) {
            return eError("server_error");
        }
        // api error
        let response = request.response;
        if (response.status !== 200) {
            // any error
            return eError(`${response.status}:${response.statusText}:${response.error}`, response.data);
        }
        // success
        return eSuccess(response.message, response.data);
    }
});

ebAPIs.getMedia = function (url) {
    if (url) return this.baseUrl + "_media/" + url;
    return null;
}