import { createTheme, responsiveFontSizes } from "@mui/material";
import { useTranslation } from "./translation";
import { eList, eColor } from "../models/core/my-js";

export function DefaultTheme() {
    const { fonts, dir } = useTranslation();

    const primary = eColor.parse("#3a3a3a");
    const secondary = eColor.parse("#0066cc");
    const fontFamily = fonts.join(", ");

    var material = createTheme({
        direction: dir,
        palette: {
            mode: "light",
            ...eList.forEach(
                {
                    primary: primary.getRGBA,
                    secondary: secondary.getRGBA,
                    white: eColor.colors.white,
                    light: eColor.parse("#eee"),
                    dark: eColor.parse("#333"),
                    black: eColor.colors.black,
                    error: eColor.colors.red,
                    warning: eColor.colors.orangered,
                    alert: eColor.colors.teal,
                    info: eColor.colors.blue,
                    success: eColor.colors.green,
                },
                {},
                (k, v, obj) => {
                    let c = eColor.parse(v);
                    obj[k] = {
                        main: c.getRGBA,
                        dark: c.darken(0.25).getRGBA,
                        light: c.lighten(0.25).getRGBA,
                        contrastText: c.getTextColor().getRGBA,
                    };
                    let cl = c.setBrightness(0.8);
                    obj[k + "2"] = {
                        main: cl.getRGBA,
                        dark: cl.darken(0.3).getRGBA,
                        light: cl.lighten(0.3).getRGBA,
                        contrastText: cl.darken(0.6).getRGBA,
                    };
                    let cd = c.setBrightness(0.2);
                    obj[k + "3"] = {
                        main: cd.getRGBA,
                        dark: cd.darken(0.3).getRGBA,
                        light: cd.lighten(0.3).getRGBA,
                        contrastText: cd.lighten(0.6).getRGBA,
                    };
                }
            ),
            text: {
                primary: primary.getRGBA,
            },
            background: {
                default: "#eee",
                paper: "#fff",
            },
        },
        typography: {
            fontFamily: fontFamily,
            fontSize: 13,
            htmlFontSize: 16,
        },
        breakpoints: {
            values: {
                xs: 0,
                sm: 576,
                md: 768,
                lg: 992,
                xl: 1200,

                mobile: 0,
                tablet: 640,
                laptop: 1024,
                desktop: 1200,
            },
        },
        components: {
            MuiButton: {
                styleOverrides: {
                    root: {
                        minWidth: "unset",// unset minwidth of buttons
                    },
                },
            },
        },
    });

    material = responsiveFontSizes(material, {
        breakpoints: ['xs', 'sm', 'md', 'lg', 'xl'],
        factor: 5
    });

    return { material: material };
}