//
import React from "react";
import {
  Typography,
  Box,
  CardActions,
  CardActionArea,
  Card,
  Stack,
} from "@mui/material";
import { ebAPIs } from "../configs/apis";
import { eNum } from "../models/core/my-js";
import { useTranslation } from "../configs/translation";
import { useTheme } from "@emotion/react";
import { useNavigate } from "react-router-dom";
import AppProductCartBtn from "./AppProductCartBtn";
import AppProductFavBtn from "./AppProductFavBtn";
import MyFloater from "../components/MyFloater";
import MyImage from "../components/MyImage";
//
const AppProductCard = ({ height, productId, product }) => {
  const { t, forLocale } = useTranslation();
  const theme = useTheme();
  const navigate = useNavigate();
  const isPriced = product?.price && product?.price > 0;
  const isOutStock = !product?.stock || product?.stock === "0";
  const subtitle = product?.model;

  return (
    <Card
      variant={"outlined"}
      sx={{
        position: "relative",
        width: "100%",
        height: height,
        bgcolor: theme.palette.white.main,
        borderColor: theme.palette.light.main,
      }}
      square
    >
      <Stack height="100%" direction="column">
        <CardActionArea
          onClick={() => {
            navigate(`/product/${productId}`);
          }}
          sx={{ flexGrow: 1 }}
        >
          <Stack height="100%" direction="column">
            <MyImage
              src={ebAPIs.getMedia(product?.thumbnail)}
              alt={product?.[forLocale({ ar: "name_alt" }, "name")]}
              height={height / 2}
              width="100%"
              objectFit="contain"
            />
            <Box sx={{ px: 1, py: 0 }}>
              <Typography
                variant="subtitle2"
                color="primary"
                sx={{
                  fontWeight: "bold",
                  display: "-webkit-box",
                  overflow: "hidden",
                  WebkitBoxOrient: "vertical",
                  WebkitLineClamp: 2,
                }}
              >
                {product?.[forLocale({ ar: "name_alt" }, "name")]}
              </Typography>

              {subtitle && (
                <Typography variant="subtitle2" color="secondary">
                  {subtitle}
                </Typography>
              )}

              {isPriced && (
                <Box>
                  <Typography
                    variant="h5"
                    component="span"
                    color={isOutStock ? "primary" : "secondary"}
                    sx={{ fontWeight: "bold" }}
                  >
                    {eNum.readable(parseFloat(product?.price), 0)}
                  </Typography>
                  <Typography
                    variant="subtitle2"
                    component="span"
                    color={isOutStock ? "primary" : "secondary"}
                    sx={{ px: 0.25 }}
                  >
                    {t("currency")}
                  </Typography>
                </Box>
              )}
            </Box>
          </Stack>
        </CardActionArea>

        {isPriced && (
          <CardActions sx={{ padding: 1 }}>
            <AppProductCartBtn
              productId={productId}
              product={product}
              full={false}
              size={"small"}
              fullWidth={true}
            />
          </CardActions>
        )}
      </Stack>

      {isPriced && (
        <MyFloater top={2} start={2}>
          <AppProductFavBtn productId={productId} full={false} size={"small"} />
        </MyFloater>
      )}
    </Card>
  );
};
export default AppProductCard;

