import { useEffect } from "react";
import { Box } from "@mui/material";
import { State } from "../../models/core/my-react";
import { ebAPIs } from "../../configs/apis";
import AppScaffold from "../../layouts/AppScaffold";

const HomePage = () => {
  const homeData = State(null);

  useEffect(() => {
    (async function () {
      if (homeData.value == null) {
        let r = await ebAPIs.callStored("public/ecommerce/home", {
          method: "POST",
        });
        if (r.success === false) {
          console.log(r.message, r.data);
          alert(r.message);
        } else {
          console.log(r.message, r.data);
          homeData.value = r.data;
        }
      }
    })();
  }, [homeData]);

  return (
    <AppScaffold>
      {homeData.value === null ? <Box>Null</Box> : <Box>NN</Box>};
    </AppScaffold>
  );
};
export default HomePage;
