import { Skeleton } from "@mui/material";
import { State } from "../models/core/my-react";

const MyImage = ({
  src,
  alt,
  fallback,
  width = "100%",
  height = "100%",
  objectFit = "contain",
  bg,
  ...props
}) => {
  const loading = State(true);
  const error = State(false);

  return (
    <>
      <img
        {...props}
        src={src}
        alt={alt}
        onLoad={() => {
          loading.value = false;
        }}
        onError={() => {
          loading.value = false;
          error.value = true;
        }}
        style={{
          ...(props?.style ?? {}),
          height,
          width,
          objectFit,
          display: loading.value === true ? "none" : "block",
          backgroundColor: bg,
        }}
      />
      {loading.value && (
        <Skeleton variant="rectangle" height={height} width={width} />
      )}
      {!fallback && error.value && "error loading image"}
      {fallback && error.value && (
        <img
          {...props}
          src={fallback}
          alt={`Fallback for ${alt}`}
          style={{
            ...(props?.style ?? {}),
            height,
            width,
            objectFit,
            backgroundColor: bg,
          }}
        />
      )}
    </>
  );
};
export default MyImage;
