import { createHashRouter } from "react-router-dom";
import { MyApp, MyAppProvider } from "./components/MyApp";
import { TranslationProvider } from "./configs/translation";

// pages
import HomePage from "./pages/home/HomePage";
import ProductPage from "./pages/product/ProductPage";
import { DefaultTheme } from "./configs/themes";
import { MyFavProvider } from "./models/MyFav";
import { MyCartProvider } from "./models/MyCart";

const App = () => {
  return (
    <MyApp
      providers={[
        MyAppProvider(TranslationProvider),
        MyAppProvider(MyCartProvider),
        MyAppProvider(MyFavProvider),
      ]}
      router={createHashRouter([
        { path: "/", element: <HomePage /> },
        { path: "/product/:productId", element: <ProductPage /> },
      ])}
      themes={DefaultTheme}
    />
  );
};
export default App;
