import { useEffect } from "react";
import { eSessionStorage } from "./core/my-js";
import { Context, State } from "./core/my-react";

export const MyFavContext = Context(() => {
    const fav = State(() => eSessionStorage.get('fav', { jsonParse: true, decode: true }) ?? {});

    // save cart to storage
    useEffect(() => {
        eSessionStorage.set('cart', fav.value, { jsonStringify: true, encode: true });
    }, [fav]);

    const getFavItem = (productId) => {
        return fav.value?.[productId] ?? null;
    };
    const setFavItem = (productId) => {
        let obj = { ...fav.value };
        obj[productId] = true;
        fav.value = obj;
    };
    const delFavItem = (productId) => {
        let obj = { ...fav.value };
        if (obj[productId]) {
            delete obj[productId];
        }
        fav.value = obj;
    };
    const toggleFavItem = (productId) => {
        let obj = { ...fav.value };
        obj[productId] = (obj[productId] !== true ? true : false);
        if (obj[productId] === false) {
            delete obj[productId];
        }
        fav.value = obj;
    };
    return {
        fav: fav.value,
        getFavItem,
        setFavItem,
        delFavItem,
        toggleFavItem,
    };
});
export const useMyFav = MyFavContext.Use;
export const MyFavProvider = MyFavContext.Provider;
export const MyFavConsumer = MyFavContext.Consumer;
export const MyFavProvCons = MyFavContext.ProvCons;