import {
  Backdrop,
  Box,
  Button,
  Card,
  Grid,
  IconButton,
  Modal,
  Paper,
  Skeleton,
  Stack,
  Zoom,
  useTheme,
} from "@mui/material";
import { useWidth } from "../../../configs/breakpoints";
import { useTranslation } from "../../../configs/translation";
import { Ref, State } from "../../../models/core/my-react";
import Carousel from "react-multi-carousel";
import { eList, eNum } from "../../../models/core/my-js";
import { ebAPIs } from "../../../configs/apis";
import { Close, Fullscreen, ZoomIn } from "@mui/icons-material";
import { useEffect, useRef } from "react";
import MyFloater from "../../../components/MyFloater";
import MyZoomView from "../../../components/MyZoomView";
import MyShareBtn from "../../../components/MyShareBtn";
import MyImage from "../../../components/MyImage";

const ProductImages = ({ product, media }) => {
  const { widthDevice } = useWidth();
  const carouselItemH = widthDevice(225, 300, 350);
  const carouselIndecS = 60;
  const carousel = Ref(null);
  const carouselPage = State(0);
  const zoomImages = State(false);

  if (!media) {
    return (
      <Skeleton
        variant="rectangular"
        width="100%"
        height={carouselItemH + carouselIndecS}
      />
    );
  } else {
    return (
      <>
        <ImagesCarousel
          {...{
            product,
            media,
            carouselItemH,
            carousel,
            carouselPage,
            zoomImages,
          }}
        />
        <ImagesIndecs
          {...{ product, media, carouselIndecS, carousel, carouselPage }}
        />
        <ImageZoom
          {...{
            product,
            media,
            carouselIndecS,
            zoomImages,
          }}
          parentCarouselPage={carouselPage}
        />
      </>
    );
  }
};
export default ProductImages;

const ImagesCarousel = ({
  product,
  media,
  carouselItemH,
  carousel,
  carouselPage,
  zoomImages,
}) => {
  const { dir, forLocale, t } = useTranslation();
  const theme = useTheme();
  const { widthDevice } = useWidth();

  return (
    <Paper
      variant="outlined"
      sx={{
        position: "relative",
        mb: 3,
        mx: 1,
        bgcolor: theme.palette.white.main,
        borderColor: theme.palette.light.main,
      }}
      square
    >
      <Carousel
        swipeable={true}
        showDots={false}
        infinite={true}
        autoPlay={!zoomImages.value}
        autoPlaySpeed={10000}
        keyBoardControl={false}
        arrows={false}
        responsive={{
          all: {
            breakpoint: { max: 999999, min: 0 },
            items: 1,
          },
        }}
        afterChange={() => {
          var i = carousel.value.state.currentSlide;
          i = eNum.loop(i - 2, 0, 0, media.length - 1);
          carouselPage.value = i;
        }}
        direction={dir}
        rtl={dir === "rtl"}
        ref={(el) => (carousel.value = el)}
      >
        {eList.generateArray(media.length, (i) => {
          return (
            <Box key={`pi-${i}`} height={carouselItemH} width="100%">
              <MyImage
                bg="white"
                width="100%"
                height="100%"
                objectFit="contain"
                src={ebAPIs.getMedia(media?.[i]?.filename)}
                alt={product?.[forLocale({ ar: "name_alt" }, "name")]}
                draggable={false}
              />
            </Box>
          );
        })}
      </Carousel>

      <MyFloater end={2} bottom={2}>
        <IconButton
          onClick={() => {
            zoomImages.value = true;
          }}
          color="secondary"
          size="small"
        >
          <ZoomIn fontSize={widthDevice("medium", null, "large")} />
        </IconButton>
      </MyFloater>

      <MyFloater end={2} top={2}>
        <MyShareBtn
          color="secondary"
          size="medium"
          fontSize="medium"
          url={window.location.href}
          title={product?.[forLocale({ ar: "name_alt" }, "name")]}
          text={
            [
              product?.[forLocale({ ar: "name_alt" }, "name")],
              product?.price &&
                `${eNum.readable(parseFloat(product?.price), 0)} ${t(
                  "currency"
                )}`,
              product?.sku && `${t("sku")}: ${product?.sku}`,
              product?.model && `${t("model")}: ${product?.model}`,
              product?.part_no && `${t("part_no")}: ${product?.part_no}`,
            ]
              .filter((o) => !!o)
              .join(", \n") + "\n"
          }
        />
      </MyFloater>
    </Paper>
  );
};

const ImagesIndecs = ({
  product,
  media,
  carouselIndecS,
  carousel,
  carouselPage,
  vertical = false,
}) => {
  const theme = useTheme();
  const { forLocale } = useTranslation();

  return (
    <Box
      width={!vertical ? "100%" : "auto"}
      height={vertical ? "100%" : "auto"}
      overflow={"auto"}
      paddingBottom={2}
    >
      <Grid
        container
        direction={!vertical ? "row" : "column"}
        justifyContent="center"
        alignItems="center"
        flexWrap="nowrap"
        width="max-content"
        height="max-content"
        spacing={2}
        px={2}
        sx={{
          minWidth: !vertical ? "100%" : "auto",
          minHeight: vertical ? "100%" : "auto",
        }}
      >
        {eList.generateArray(media.length, (i) => {
          return (
            <Grid
              key={`pii-${i}`}
              item
              width={carouselIndecS}
              height={carouselIndecS}
            >
              <Card
                onClick={() => {
                  if (carousel.value) {
                    carousel.value.goToSlide(i + 2);
                  }
                }}
                variant="outlined"
                sx={{
                  cursor: "pointer",
                  width: "100%",
                  height: "100%",
                  overflow: "hidden",
                  borderWidth: 2,
                  bgcolor: theme.palette.white.main,
                  borderColor:
                    carouselPage.value === i
                      ? theme.palette.secondary.main
                      : theme.palette.light.main,
                }}
              >
                <MyImage
                  bg="white"
                  width="100%"
                  height="100%"
                  objectFit="cover"
                  src={ebAPIs.getMedia(media?.[i]?.filename)}
                  alt={product?.[forLocale({ ar: "name_alt" }, "name")]}
                  draggable={false}
                  loading="lazy"
                />
              </Card>
            </Grid>
          );
        })}
      </Grid>
    </Box>
  );
};

const ImageZoom = ({
  product,
  media,
  carouselIndecS,
  zoomImages,
  parentCarouselPage,
}) => {
  const { widthDevice } = useWidth();
  const { forLocale, dir } = useTranslation();
  const theme = useTheme();
  const carousel = useRef(null);
  const carouselPage = State(0);
  const zoomview = useRef(null);

  // reset zoom view
  useEffect(() => {
    if (zoomview.current) {
      zoomview.current.resetZoom();
    }
    // eslint-disable-next-line
  }, [carouselPage]);

  // reset zoom view
  useEffect(() => {
    if (carousel.value) {
      carousel.value.goToSlide(parentCarouselPage.value + 2);
    }
    // eslint-disable-next-line
  }, [parentCarouselPage]);

  return (
    <Modal
      keepMounted
      open={zoomImages.value}
      onClose={() => {
        if (zoomview.value) {
          zoomview.value.resetZoom();
        }
        zoomImages.value = false;
      }}
      closeAfterTransition
      slots={{ backdrop: Backdrop }}
      slotProps={{
        backdrop: {
          timeout: 500,
        },
      }}
      sx={{
        width: "100%",
        height: "100%",
        padding: widthDevice(0, 0, 5, 10),
      }}
    >
      <Zoom in={zoomImages.value}>
        <Box
          height="100%"
          width={widthDevice("100%", null, "75%", "50%")}
          sx={{
            mx: "auto",
            bgcolor: theme.palette.white.main,
            position: "relative",
            borderRadius: 2,
            overflow: "hidden",
          }}
        >
          <Stack
            direction={widthDevice("column", "row-reverse")}
            sx={{ height: "100%", width: "100%" }}
            spacing={1}
          >
            <Paper
              variant="outlined"
              height="100%"
              width="100%"
              sx={{
                position: "relative",
                flexGrow: 1,
                flexShrink: 1,
                flexBasis: 0,
                overflow: "hidden",
              }}
              square
            >
              <MyZoomView ref={zoomview} minZoom={1} maxZoom={5}>
                <Carousel
                  className="fill"
                  sliderClass={"h-100"}
                  swipeable={false}
                  showDots={false}
                  infinite={true}
                  autoPlay={false}
                  keyBoardControl={false}
                  arrows={false}
                  responsive={{
                    all: {
                      breakpoint: { max: 999999, min: 0 },
                      items: 1,
                    },
                  }}
                  afterChange={() => {
                    var i = carousel.value.state.currentSlide;
                    i = eNum.loop(i - 2, 0, 0, media.length - 1);
                    carouselPage.value = i;
                  }}
                  direction={dir}
                  rtl={dir === "rtl"}
                  ref={(el) => (carousel.value = el)}
                >
                  {eList.generateArray(media.length, (i) => {
                    return (
                      <MyImage
                        key={`pi-${i}`}
                        bg="white"
                        width="100%"
                        height="100%"
                        objectFit="contain"
                        src={ebAPIs.getMedia(media?.[i]?.filename)}
                        alt={product?.[forLocale({ ar: "name_alt" }, "name")]}
                        draggable={false}
                        loading="lazy"
                      />
                    );
                  })}
                </Carousel>
              </MyZoomView>

              <MyFloater bottom={5} end={5}>
                <Button
                  onClick={() => {
                    if (zoomview.current) {
                      zoomview.current.resetZoom();
                    }
                  }}
                  disableElevation
                  variant="contained"
                  color="secondary2"
                  size="small"
                  sx={{ padding: 0 }}
                >
                  <Fullscreen fontSize={"large"} />
                </Button>
              </MyFloater>
            </Paper>

            <ImagesIndecs
              {...{ product, media, carouselIndecS, carousel, carouselPage }}
              vertical={widthDevice(false, true)}
            />
          </Stack>

          <MyFloater top={5} end={5}>
            <Button
              onClick={() => {
                zoomImages.value = false;
              }}
              disableElevation
              variant="contained"
              color="error2"
              size="medium"
              sx={{ padding: 0 }}
            >
              <Close fontSize={"large"} />
            </Button>
          </MyFloater>
        </Box>
      </Zoom>
    </Modal>
  );
};
