//
import React from "react";
import {
  AppBar,
  Toolbar,
  Typography,
  IconButton,
  Button,
  useTheme,
} from "@mui/material";
import { ArrowBack, ArrowForward, Language } from "@mui/icons-material";
import { useNavigate } from "react-router-dom";
import { useTranslation } from "../configs/translation";
import { useBreakpoints } from "../configs/breakpoints";
//

const AppHeader = ({ title, main }) => {
  const { only } = useBreakpoints();
  const navigate = useNavigate();
  const theme = useTheme();
  const { locale, locales, setLocale, forDir } = useTranslation();

  if (only("mobile")) {
    if (main === true) {
      return MobileMainHeader({
        navigate,
        theme,
        locale,
        locales,
        setLocale,
        forDir,
      });
    } else {
      return MobileSubHeader({
        title,
        navigate,
        theme,
        locale,
        locales,
        setLocale,
        forDir,
      });
    }
  } else {
    return <div>header</div>;
  }
};
export default AppHeader;

const MobileMainHeader = ({ navigate }) => {
  return <div>main mobile</div>;
};
const MobileSubHeader = ({
  title,
  navigate,
  theme,
  locale,
  locales,
  setLocale,
  forDir,
}) => {
  return (
    <AppBar position="static" variant="dense" color="white" sx={{ mb: 1 }}>
      <Toolbar
        variant="dense"
        sx={{
          borderBottomWidth: 1,
          borderBottomStyle: "solid",
          borderBottomColor: theme.palette.light.main,
          px: 0.5,
        }}
        disableGutters
      >
        <Button
          onClick={() => {
            navigate(-1);
          }}
          size="small"
          variant="contained"
          color="light"
          sx={{ p: 0.5 }}
          disableElevation
        >
          {forDir(
            <ArrowBack fontSize="small" />,
            <ArrowForward fontSize="small" />
          )}
        </Button>
        <Typography
          variant="subtitle1"
          textAlign={"center"}
          sx={{ flexGrow: 1, px: 1 }}
          noWrap
        >
          {title}
        </Typography>
        <IconButton
          onClick={() => {
            var lk = Object.keys(locales);
            var l = lk.indexOf(locale);
            lk.splice(l, 1);

            setLocale(lk[0]);
          }}
          size="small"
          variant="outlined"
          color="secondary"
        >
          <Language fontSize="small" />
        </IconButton>
      </Toolbar>
    </AppBar>
  );
};
