import { useEffect } from "react";
import { eSessionStorage } from "./core/my-js";
import { Context, State } from "./core/my-react";

export const MyCartContext = Context(() => {
    const cart = State(() => eSessionStorage.get('cart', { jsonParse: true, decode: true }) ?? {});

    // save cart to storage
    useEffect(() => {
        eSessionStorage.set('cart', cart.value, { jsonStringify: true, encode: true });
    }, [cart]);

    const fromProduct = (product) => {
        return {
            min: parseInt(product?.min_order_quantity ?? 1),
            step: parseInt(product?.step_order_quantity ?? 1),
            max: parseInt(Math.min(product?.max_order_quantity ?? product?.stock ?? 0, product?.stock ?? 0)),
            stock: product?.stock,
            reserve: (!product?.stock || product?.stock === "0") && product?.per_order === "1"
        };
    }

    const getCartItem = (productId) => {
        return cart.value?.[productId] ?? null;
    };
    const addCartItem = (productId, product) => {
        var { min, step, max, reserve } = fromProduct(product);
        let obj = { ...cart.value };

        let qty = parseInt(obj[productId] ?? 0);
        qty += step;
        if (qty < min) {
            qty = min;
        }
        if (!reserve && qty > max) {
            qty = max;
        }
        obj[productId] = qty;
        if (qty === 0) {
            delete obj[productId];
        }
        cart.value = obj;
    };
    const subCartItem = (productId, product) => {
        var { min, step, max, reserve } = fromProduct(product);
        let obj = { ...cart.value };

        let qty = parseInt(obj[productId] ?? 0);
        qty -= step;
        if (!reserve && qty > max) {
            qty = max;
        }
        if (qty < min) {
            qty = 0;
        }
        obj[productId] = qty;
        if (qty === 0) {
            delete obj[productId];
        }

        cart.value = obj;
    };
    const delCartItem = (productId) => {
        let obj = { ...cart.value };
        if (obj[productId]) {
            delete obj[productId];
        }
        cart.value = obj;
    };
    return {
        cart: cart.value,
        getCartItem,
        addCartItem,
        subCartItem,
        delCartItem
    };
});
export const useMyCart = MyCartContext.Use;
export const MyCartProvider = MyCartContext.Provider;
export const MyCartConsumer = MyCartContext.Consumer;
export const MyCartProvCons = MyCartContext.ProvCons;