import { MyGrid, MyGridItem } from "../components/MyGrid";
import {
  crossAlign,
  crossItemAlign,
  mainAlign,
  mainItemAlign,
} from "../components/configs";
import { eList } from "../models/core/my-js";
import AppProductCard from "./AppProductCard";

const AppProductCardGrid = ({ products }) => {
  const cardHeight = 300;

  return (
    <MyGrid
      cols={{ xs: 2, sm: 3, md: 4, lg: 5 }}
      rowHeight={cardHeight}
      spacing={1}
      main={mainAlign.center}
      cross={crossAlign.center}
      mainItem={mainItemAlign.center}
      crossItem={crossItemAlign.center}
    >
      {eList.generateArray(products?.length, (index) => {
        let product = products?.[index];
        return (
          <MyGridItem key={`ps-${index}`}>
            <AppProductCard
              productId={product?.id}
              product={product}
              height={cardHeight}
            />
          </MyGridItem>
        );
      })}
    </MyGrid>
  );
};
export default AppProductCardGrid;
