// class
import { eTranslation, eLocale } from "../models/core/my-js";
import { createTranslationContext } from "../models/core/my-react";
// json
import en from "../assets/locales/en.json";
import ar from "../assets/locales/ar.json";
//==============================< Translation Class
export const Translation = new eTranslation([
    new eLocale("en", "ltr", "English", ['"Mulish"', '"Tajawal"', "sans-serif"], en),
    new eLocale("ar", "rtl", "", ['"Tajawal"', '"Mulish"', "sans-serif"], ar),
]);
export const TranslationContext = createTranslationContext(Translation);
export const useTranslation = TranslationContext.Use;
export const TranslationProvider = TranslationContext.Provider;
export const TranslationConsumer = TranslationContext.Consumer;