import { Favorite, FavoriteBorder } from "@mui/icons-material";
import { Button, IconButton } from "@mui/material";
import { useMyFav } from "../models/MyFav";
import { useTranslation } from "../configs/translation";

const AppProductFavBtn = ({
  productId,
  full = false,
  size = "medium",
  fullWidth = false,
}) => {
  const { t } = useTranslation();
  const { getFavItem, toggleFavItem } = useMyFav();

  if (full) {
    return (
      <Button
        onClick={() => {
          toggleFavItem(productId);
        }}
        variant="text"
        color={getFavItem(productId) ? "secondary" : "primary"}
        startIcon={getFavItem(productId) ? <Favorite /> : <FavoriteBorder />}
        size={size}
        fullWidth={fullWidth}
      >
        {getFavItem(productId)
          ? t("remove_from_favourites")
          : t("add_to_favourites")}
      </Button>
    );
  } else {
    return (
      <IconButton
        onClick={() => {
          toggleFavItem(productId);
        }}
        color={getFavItem(productId) ? "secondary" : "primary"}
        size={size}
      >
        {getFavItem(productId) ? <Favorite /> : <FavoriteBorder />}
      </IconButton>
    );
  }
};
export default AppProductFavBtn;
