import { forwardRef, useEffect } from "react";
import { Ref, RefState } from "../models/core/my-react";
import { Box } from "@mui/material";
import { eDom, eNum } from "../models/core/my-js";

const MyZoomView = forwardRef(({ children, minZoom = 1, maxZoom = 5 }, ref) => {
  const zoom = RefState(1);
  const posx = RefState(null);
  const posy = RefState(null);

  const pinchDist = Ref(null);
  const lastTapT = Ref(0);
  const lastTapP = Ref({ x: null, y: null });

  // on mounted
  useEffect(() => {
    const onWheel = eDom.eventPreventDefault((evt) => {
      // zoom
      zoom.value = eNum.clamp(zoom.value - evt.deltaY / 4, minZoom, maxZoom);
      // pos
      const { left, top } = ref.current.getBoundingClientRect();
      posx.value = evt.pageX - left - window.scrollX;
      posy.value = evt.pageY - top - window.scrollY;
    });
    const onDblClick = eDom.eventPreventDefault((evt) => {
      // zoom
      zoom.value = eNum.clamp(zoom.value * 1.75, minZoom, maxZoom);
    });
    const onMouseMove = (evt) => {
      const { left, top, width, height } = ref.current.getBoundingClientRect();
      let x = evt.pageX - left - window.scrollX;
      let y = evt.pageY - top - window.scrollY;
      posx.value = x + (x - width / 2) / 4;
      posy.value = y + (y - height / 2) / 4;
    };
    const onTouchMove = (evt) => {
      if (evt.touches.length > 1) {
        return eDom.eventPreventDefault((evt) => {
          //zoom
          if (!pinchDist.value) pinchDist.value = getDistance(evt.touches);
          let delta = pinchDist.value - getDistance(evt.touches);
          zoom.value = eNum.clamp(zoom.value - delta / 50, minZoom, maxZoom);
          // pos
          const { left, top } = ref.current.getBoundingClientRect();
          let p = getMidpoint(evt.touches);
          posx.value = p.x - left - window.scrollX;
          posy.value = p.y - top - window.scrollY;
        })(evt);
      } else {
        const touch = evt.targetTouches[0];
        const { left, top, width, height } =
          ref.current.getBoundingClientRect();
        let x = touch.pageX - left - window.scrollX;
        let y = touch.pageY - top - window.scrollY;
        posx.value = x + (x - width / 2) / 4;
        posy.value = y + (y - height / 2) / 4;

        pinchDist.value = null;
      }
      function getDistance(touches) {
        const touch1 = touches[0];
        const touch2 = touches[1];

        const xDiff = touch2.clientX - touch1.clientX;
        const yDiff = touch2.clientY - touch1.clientY;

        return Math.sqrt(xDiff * xDiff + yDiff * yDiff);
      }
      function getMidpoint(touches) {
        const midpointX = (touches[0].pageX + touches[1].pageX) / 2;
        const midpointY = (touches[0].pageY + touches[1].pageY) / 2;

        return { x: midpointX, y: midpointY };
      }
    };
    const onTouchEnd = (evt) => {
      const currentTime = new Date().getTime();
      const tapPosition = {
        x: evt.changedTouches[0].clientX,
        y: evt.changedTouches[0].clientY,
      };

      if (
        currentTime - lastTapT.value < 300 &&
        isWithinThreshold(tapPosition, lastTapP.value, 20)
      ) {
        // zoom
        zoom.value = eNum.clamp(zoom.value * 1.75, minZoom, maxZoom);

        lastTapT.value = 0;
      } else {
        // Not a double tap, remember this tap's time and position
        lastTapT.value = currentTime;
        lastTapP.value = tapPosition;
      }

      function isWithinThreshold(pos1, pos2, thresh) {
        const dx = pos1.x - pos2.x;
        const dy = pos1.y - pos2.y;
        return Math.sqrt(dx * dx + dy * dy) < thresh;
      }
    };
    // set pos
    const { width, height } = ref.current.getBoundingClientRect();
    posx.value = height / 2;
    posy.value = width / 2;

    // reset current func
    if (ref.current) {
      ref.current.resetZoom = () => {
        zoom.value = 1;
        if (ref.current) {
          const { width, height } = ref.current.getBoundingClientRect();
          posx.value = height / 2;
          posy.value = width / 2;
        }
      };
    }

    // container listeners
    const cr = ref.current;
    // add listners
    if (cr) {
      cr.addEventListener("wheel", onWheel, { passive: false });
      cr.addEventListener("dblclick", onDblClick, { passive: false });
      cr.addEventListener("mousemove", onMouseMove, { passive: false });
      cr.addEventListener("touchstart", onTouchMove, { passive: false });
      cr.addEventListener("touchmove", onTouchMove, { passive: false });
      cr.addEventListener("touchend", onTouchEnd, { passive: false });
    }

    return () => {
      // Remove the event listener when the component unmounts
      if (cr) {
        cr.removeEventListener("wheel", onWheel, { passive: false });
        cr.removeEventListener("dblclick", onDblClick, { passive: false });
        cr.removeEventListener("mousemove", onMouseMove, { passive: false });
        cr.removeEventListener("touchstart", onTouchMove, { passive: false });
        cr.removeEventListener("touchmove", onTouchMove, { passive: false });
        cr.removeEventListener("touchend", onTouchEnd, { passive: false });
      }
    };
    // eslint-disable-next-line
  }, []);

  return (
    <Box
      ref={ref}
      height="100%"
      width="100%"
      sx={{
        userSelect: "none",
        display: "inline-block",
        position: "relative",
        overflow: "hidden",
        cursor: "all-scroll",
      }}
    >
      <Box
        height="100%"
        width="100%"
        sx={{
          pointerEvents: "none",
          transform: `scale(${zoom.state.value});`,
          transformOrigin: `${posx.state.value}px ${posy.state.value}px;`,
        }}
      >
        {children}
      </Box>
    </Box>
  );
});
export default MyZoomView;
