import { useEffect } from "react";
import {
  Box,
  Stack,
  Grid,
  Skeleton,
  useTheme,
  Typography,
  Divider,
  Tab,
  Tabs,
} from "@mui/material";
import { State } from "../../models/core/my-react";
import { ebAPIs } from "../../configs/apis";
import AppScaffold from "../../layouts/AppScaffold";
import { useNavigate, useParams } from "react-router-dom";
import { useTranslation } from "../../configs/translation";
import { eList, eNum } from "../../models/core/my-js";
import "react-multi-carousel/lib/styles.css";
import { TabContext, TabPanel } from "@mui/lab";
import AppProductFavBtn from "../../layouts/AppProductFavBtn";
import AppProductCartBtn from "../../layouts/AppProductCartBtn";
import AppProductCardGrid from "../../layouts/AppProductCardGrid";
import AppHtmlViewer from "../../layouts/AppHtmlViewer";
import { useWidth } from "../../configs/breakpoints";
import ProductImages from "./layouts/ProductImages";

const ProductPage = () => {
  // navigation
  const navigate = useNavigate();
  const { t, forLocale } = useTranslation();
  const { productId } = useParams();
  const productData = State(null);
  const similarData = State(null);

  // product id change
  useEffect(() => {
    // no product, go home
    if (!productId) {
      navigate("/");
    }

    window.scrollTo(0, 0);
    productData.value = null;
    similarData.value = null;
    // eslint-disable-next-line
  }, [productId]);

  // get product from api
  useEffect(() => {
    (async function () {
      if (productData.value == null) {
        let r = await ebAPIs.callStored("public/ecommerce/products/details", {
          method: "POST",
          bodyData: { product_id: productId },
          namespace: productId,
        });
        if (r.success === false) {
          console.warn(r.message, r.data);
          alert(r.message);
        } else {
          productData.value = r.data;
        }
      }
    })();
  }, [productData, productId, navigate]);

  // get similar from api
  useEffect(() => {
    (async function () {
      if (similarData?.value == null) {
        let r = await ebAPIs.callStored("public/ecommerce/products/similar", {
          method: "POST",
          bodyData: { product_id: productId },
          namespace: productId,
        });
        if (r.success === false) {
          console.warn(r.message, r.data);
          alert(r.message);
        } else {
          similarData.value = r.data;
        }
      }
    })();
  }, [similarData, productId]);

  // return
  return (
    <AppScaffold
      contentScrollable={productData.value !== null}
      title={`${t("product")} ${
        productData.value === null
          ? ""
          : ` | ${
              productData.value?.product?.[
                forLocale({ ar: "name_alt" }, "name")
              ]
            }`
      }`}
      headerTitle={
        productData.value?.product?.[forLocale({ ar: "name_alt" }, "name")]
      }
      main={false}
    >
      <Stack spacing={2}>
        <Box>
          <Grid container spacing={2}>
            <Grid item xs={12} md={6}>
              <ProductImages
                product={productData.value?.product}
                media={productData.value?.media}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <ProductTitle
                productId={productId}
                product={productData.value?.product}
              />
            </Grid>
          </Grid>
        </Box>
        <ProductDesc product={productData.value?.product} />
        <ProductSimilar products={similarData.value?.products} />
      </Stack>
    </AppScaffold>
  );
};
export default ProductPage;

function ProductTitle({ productId, product }) {
  const { t, forLocale } = useTranslation();
  const isPriced = product?.price && product?.price > 0;
  const isOutStock = !product?.stock || product?.stock === "0";
  const { widthDevice } = useWidth();

  if (!product) {
    return (
      <Stack spacing={3} px={1} mt={1}>
        <Skeleton variant="rectangular" width="90%" height={25} />
        <Skeleton variant="rectangular" width="40%" height={15} />

        <Divider />

        <Skeleton variant="rectangular" width="50%" height={15} />
        <Skeleton variant="rectangular" width="50%" height={15} />
        <Skeleton variant="rectangular" width="50%" height={15} />
        <Skeleton variant="rectangular" width="75%" height={50} />

        <Divider />

        <Skeleton variant="rectangular" width="75%" height={30} />
      </Stack>
    );
  } else {
    return (
      <Stack spacing={3} px={1} mt={1}>
        <Stack spacing={3} px={1}>
          <Typography variant="h5" sx={{ fontWeight: "bold" }}>
            {product?.[forLocale({ ar: "name_alt" }, "name")]}
          </Typography>

          {isPriced && (
            <Box>
              <Typography
                variant="h4"
                component="span"
                color={isOutStock ? "primary" : "secondary"}
                sx={{ fontWeight: "bold" }}
              >
                {eNum.readable(parseFloat(product?.price), 0)}
              </Typography>
              <Typography
                variant="subtitle1"
                component="span"
                color={isOutStock ? "primary" : "secondary"}
                sx={{ px: 0.25 }}
              >
                {t("currency")}
              </Typography>
            </Box>
          )}
        </Stack>

        {!product?.sku &&
        !product?.model &&
        !product?.part_no &&
        !product?.brief ? null : (
          <Divider />
        )}

        <Stack spacing={3} px={1}>
          {product?.sku && (
            <Box>
              <Typography variant="subtitle1" component="span">
                {t("sku") + ":"}
              </Typography>
              <Typography
                variant="h6"
                component="span"
                sx={{ fontWeight: "bold", px: 1 }}
              >
                {product?.sku}
              </Typography>
            </Box>
          )}

          {product?.model && (
            <Box>
              <Typography variant="subtitle1" component="span">
                {t("model") + ":"}
              </Typography>
              <Typography
                variant="h6"
                component="span"
                sx={{ fontWeight: "bold", px: 1 }}
              >
                {product?.model}
              </Typography>
            </Box>
          )}

          {product?.part_no && (
            <Box>
              <Typography variant="subtitle1" component="span">
                {t("part_no") + ":"}
              </Typography>
              <Typography
                variant="h6"
                component="span"
                sx={{ fontWeight: "bold", px: 1 }}
              >
                {product?.part_no}
              </Typography>
            </Box>
          )}

          {product?.brief && (
            <Box>
              <AppHtmlViewer
                html={product?.[forLocale({ ar: "brief_alt" }, "brief")]}
              />
            </Box>
          )}
        </Stack>

        {isPriced && <Divider />}

        {isPriced && (
          <Stack
            direction={widthDevice("column", null, "row")}
            alignItems={widthDevice("center", "start")}
            spacing={2}
          >
            <AppProductCartBtn
              productId={productId}
              product={product}
              full={true}
              size={"large"}
            />
            <AppProductFavBtn
              productId={productId}
              full={true}
              size={"large"}
            />
          </Stack>
        )}
      </Stack>
    );
  }
}

function ProductDesc({ product }) {
  const { t } = useTranslation();
  const theme = useTheme();
  const tabPage = State("0");

  if (!product) {
    return (
      <Stack spacing={3} px={2}>
        <Divider />
        <Skeleton variant="rectangular" width="100%" height={300} />
      </Stack>
    );
  } else if (!product?.desc && !product?.tech && !product?.content) {
    return null;
  } else {
    let k = [];
    let v = [];
    if (product?.desc) {
      k.push(t("desc"));
      v.push(product?.desc);
    }
    if (product?.tech) {
      k.push(t("tech_info"));
      v.push(product?.tech);
    }
    if (product?.content) {
      k.push(t("package_contents"));
      v.push(product?.content);
    }

    return (
      <Stack spacing={2} px={1} mt={1}>
        <Divider />
        <TabContext value={tabPage.value}>
          <Tabs
            value={tabPage.value}
            onChange={(evt, v) => (tabPage.value = v)}
            centered
            textColor="secondary"
            indicatorColor="secondary"
          >
            {eList.generateArray(k.length, (index) => (
              <Tab
                key={`pdk-${index}`}
                label={k?.[index]}
                value={index.toString()}
                sx={{ fontWeight: "bold" }}
              />
            ))}
          </Tabs>
          <Box sx={{ bgcolor: theme.palette.light.main }}>
            {eList.generateArray(v.length, (index) => (
              <TabPanel key={`pdv-${index}`} value={index.toString()}>
                <AppHtmlViewer html={v?.[index]} />
              </TabPanel>
            ))}
          </Box>
        </TabContext>
      </Stack>
    );
  }
}

function ProductSimilar({ products }) {
  const { t } = useTranslation();

  if (!products) {
    return (
      <Stack spacing={3} px={2}>
        <Divider />
        <Skeleton variant="rectangular" width="100%" height={500} />
      </Stack>
    );
  } else if (!products?.length || products?.length === 0) {
    return null;
  } else {
    return (
      <Stack spacing={2} px={1} mt={1}>
        <Divider>
          <Typography variant="h6">{t("similar_products")}</Typography>
        </Divider>
        <AppProductCardGrid products={products} />
      </Stack>
    );
  }
}
