// packages
import React from "react";
//==============================< State
export const State = (initValue) => {
  const [getV, setV] = React.useState(initValue);
  return {
    get value() {
      return getV;
    },
    set value(value) {
      setV(value);
      return value;
    },
  };
};
//==============================< Ref
export const Ref = (initValue) => {
  const get = React.useRef(initValue);
  return {
    get value() {
      return get.current;
    },
    set value(value) {
      get.current = value;
      return get.current;
    },
  };
};
//==============================< stateRef
export const RefState = (initValue) => {
  const state = State(initValue);
  const ref = Ref(initValue);

  return {
    state,
    ref,
    get value() {
      return ref.value;
    },
    set value(value) {
      ref.value = value;
      state.value = value;
      return ref.value;

    }
  };
}
//==============================< Context
export const Context = (func = () => { }) => {
  const Context = React.createContext({});

  return {
    Context: Context,
    Use: () => {
      const ctx = React.useContext(Context);

      if (!ctx) {
        throw new Error('context must be used within a MyProvider');
      }

      return ctx;
    },
    Provider: (p) => {
      return <Context.Provider value={func()} children={p.children} />;
    },
    Consumer: (child = (obj) => { }) => {
      return <Context.Consumer children={child} />;
    },
    ProvCons: (child = (obj) => { }) => {
      return <Context.Provider value={func()} children={<Context.Consumer children={child} />} />;
    },
  };
};
//==============================< Translation
export function createTranslationContext(translation) {
  return Context(() => {
    const locale = State(translation.locale.code);
    const dir = State(translation.locale.dir);
    const fonts = State(translation.locale.fonts);
    const locales = State(translation.locales);

    const setLocale = (newLocale) => {
      translation.setLocale(newLocale, { reloadPage: false });
      locale.value = translation.locale.code;
      dir.value = translation.locale.dir;
      fonts.value = translation.locale.fonts;
      locales.value = translation.locales;
    };

    const forLocale = (options, fallback) => {
      if (options?.[locale.value]) {
        return options?.[locale.value];
      }
      return fallback;
    };

    const forDir = (ltr, rtl) => {
      return (dir.value === "rtl" ? rtl : ltr);
    };

    return {
      locale: locale.value,
      dir: dir.value,
      fonts: fonts.value,
      locales: locales.value,
      setLocale,
      forLocale,
      forDir,
      t: translation.translate.bind(translation),
    };
  });
}